<template>
   <div id="deleteModel" ref="deleteModel" class="modal fade" tabindex="-1"
        aria-labelledby="delProductModalLabel" aria-hidden="true">
     <div class="modal-dialog">
       <div v-if="JSON.stringify(delobject) !== '{}'"   class="modal-content border-0">
         <div  class="modal-header bg-danger text-white">
           <h5 id="delProductModalLabel" class="modal-title">
             <span>刪除{{delobject.title}}</span>
           </h5>
           <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
         </div>
         <div class="modal-body">
           是否刪除
           <strong class="text-danger">{{delobject.bodyInfo}}</strong> (刪除後將無法恢復)。
         </div>
         <div class="modal-footer">
           <button type="button" class="btn btn-outline-secondary" data-bs-dismiss="modal">
             取消
           </button>
           <button type="button" class="btn btn-danger" @click="clickDelete">
             確認刪除
           </button>
         </div>
       </div>
     </div>
   </div>
</template>

<script>
import Modal from 'bootstrap/js/dist/modal';

export default {
  data() {
    return {
      modal: '',
    };
  },
  props: {
    delobject: {
      type: Object,
      default() { return {}; },
    },
  },
  mounted() {
    this.modal = new Modal(this.$refs.deleteModel, {
      keyboard: false,
    });
  },
  methods: {
    open() {
      // console.log(id.productName);
      this.modal.show();
    },
    close() {
      this.modal.hide();
    },
    clickDelete() {
      this.$emit('clickDelete');
    },
  },
};
</script>
